import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { AuthenticationGuard } from '../guards/authentication.guard/authentication.guard';
import { ChildEssentialsModule } from './child.essential.module';
import { DirectivesModule } from './directives.module';

import { StkwithdrawalEntryComponent, StockWthDetailComponent } from '../components/transaction/stkwithdrawal-entry/stkwithdrawal-entry.component';
import { StkwithdrawalModifyComponent } from '../components/transaction/stkwithdrawal-modify/stkwithdrawal-modify.component';
import { StkwithdrawalModifycontentComponent, StockWthModifyDetailComponent } from '../components/transaction/stkwithdrawal-modifycontent/stkwithdrawal-modifycontent.component';
import { StkwthApprovalComponent } from '../components/approvals/stkwth-approval/stkwth-approval.component';
import { StkwthLrComponent } from '../components/transaction/stkwth-lr/stkwth-lr.component';

const routes: Routes = [
  { path: "entry", component: StkwithdrawalEntryComponent, canActivate: [AuthenticationGuard] },
  { path: "modify", component: StkwithdrawalModifyComponent, canActivate: [AuthenticationGuard] },
  { path: "modifyContent/:headerId", component: StkwithdrawalModifycontentComponent, canActivate: [AuthenticationGuard] },
  { path: "self-approval", component: StkwthApprovalComponent, canActivate: [AuthenticationGuard] },
  { path: "lr", component: StkwthLrComponent, canActivate: [AuthenticationGuard] },
];

@NgModule({
  imports: [
    RouterModule.forChild(routes),
    CommonModule,
    ChildEssentialsModule,
    DirectivesModule
  ],
  exports: [
    RouterModule
  ],
  declarations: [
    StkwithdrawalEntryComponent,
    StockWthDetailComponent,
    StkwithdrawalModifyComponent,
    StkwithdrawalModifycontentComponent,
    StockWthModifyDetailComponent,
    StkwthApprovalComponent,
    StkwthLrComponent
  ]
})
export class StkwithdrawalModule { }
