import { Component, OnInit, Inject, ViewChild, ElementRef, HostListener, Console } from '@angular/core';
import { FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { MedicoUtility } from '../../../classes/medico-utility';
import { MedicoService } from '../../../services/medico/medico.service';
import { MedicoSession } from '../../../classes/medico-session';
import { Constants } from '../../../classes/constants';
import { StkWithdrawalService } from '../../../services/stk-withdrawal/stk-withdrawal.service';
import { MatTable, MatTableDataSource } from '@angular/material/table';
import { MatPaginator } from '@angular/material/paginator';
import { MatDialog  } from '@angular/material/dialog';
import { DatePipe } from '@angular/common'

@Component({
  selector: 'app-stkwithdrawal-modify',
  templateUrl: './stkwithdrawal-modify.component.html',
  styleUrls: ['./stkwithdrawal-modify.component.css']
})
export class StkwithdrawalModifyComponent implements OnInit {

  session : MedicoSession;
  constants : Constants = new Constants();

  maxDate:Date;
  stkWthList !:  MatTableDataSource<any>;

  @ViewChild(MatTable) table!: MatTable<any>;
  @ViewChild(MatPaginator) paginator!: MatPaginator;
  displayedColumns: string[] = ["ChallanNumber","ChallanDate","WithdrawalType","SenderName","State","Remove"];

  form : FormGroup;

  constructor(
    public stkWithdrawalService : StkWithdrawalService,
    private medicoUtility : MedicoUtility,
    public medicoService: MedicoService,
    public fb: FormBuilder,
    public dialog: MatDialog,
    private el: ElementRef,
    public datepipe: DatePipe
  ) {

    this.form = this.fb.group({
      fromDate: new FormControl(new Date(), Validators.required),
      toDate: new FormControl(new Date(), Validators.required),
    });
    this.medicoService.headingName = "Stock Withdrawal Modify";
    this.medicoUtility.toggleProgressBar(true);
    this.session = this.medicoUtility.getSessionVariables();
    this.maxDate=new Date();
    this.medicoUtility.toggleProgressBar(false);

  }

  ngOnInit() {
  }

  searchStockWithdrawls() {
    if(this.form.get("fromDate")?.valid && this.form.get("toDate")?.valid) {
      this.medicoUtility.toggleProgressBar(true);
      let fDate =this.datepipe.transform(this.form.get("fromDate")?.value, 'dd/MM/yyyy');
      let tDate =this.datepipe.transform(this.form.get("toDate")?.value, 'dd/MM/yyyy');

      this.stkWithdrawalService.getSwvList(this.session.EMP_ID,this.session.USER_TYPE,fDate as string,tDate as string ).subscribe(response => {
         this.stkWthList = new MatTableDataSource(response.stkWthList.slice());
         this.stkWthList.paginator = this.paginator;
         this.medicoUtility.toggleProgressBar(false);
       }); 
    }
  }

  removeStkWithdrawal(headerId :any,index:any) {
    this.medicoService.confirmBox("Confirmation", "Are you sure to delete this Stock Withdrawal?",
    "Ok", "Cancel")
    .subscribe(result => {
      if (result == true) {
        this.deleteStockWithdrawal(headerId,index);
      }
    });
  }

  deleteStockWithdrawal(headerId :any,index:any) {
    this.medicoUtility.toggleProgressBar(true);
    try {
    this.stkWithdrawalService.deleteStkWithdrawal(this.session.EMP_ID,headerId).subscribe(response => {
       const data = this.stkWthList.data;
        data.splice((this.paginator.pageIndex * this.paginator.pageSize) + index, 1);
        this.stkWthList.data = data;
       this.medicoService.popup("Message", "Stock Withdrawal deleted successfully");
       this.medicoUtility.toggleProgressBar(false);
      }, (error) => {
        this.medicoUtility.toggleProgressBar(false);
        this.medicoService.popup("Message", "Network Error Occurred!");
     }); 
    } catch (error) {
      this.medicoUtility.toggleProgressBar(false);
      this.medicoService.popup("Message", "Network Error Occurred!");
    }

  }

}
